import { Button } from "evergreen-ui";
import React, { useState } from "react";
import { Table } from "reactstrap";
import Row from "../../../../component/Row/Row";
import SearchWord from "../../../../component/SearchWord/SearchWord";
import { useOwnDictionary } from "../../../../hooks/useOwnDictionary";
import { generateString } from "../../../../specialFunction/specialFunction";
import DataWords from "./DataWords";
import "./OwnDictionary.scss";

const OwnDictionary: React.FC = () => {
  const ownDictionary: any = useOwnDictionary();

  const [active, setActive] = useState("");
  const [activePart, setActivePart] = useState("1 part");
  const [activePartOptions, setActivePartOptions] = useState("");

  const setNewActive = (word: string) => active === word? setActive("") : setActive(word);

  const setNewActivePartOptions = (part: string) => activePartOptions === part? setActivePartOptions("") : setActivePartOptions(part);

  const listWords = (ownDictionary: any) => {
    const idx = [...ownDictionary].findIndex((item: any) => item.part === activePart);

    return ownDictionary[idx]?.words?.map((word: any) => (
      <DataWords data={word} active={active} newActive={setNewActive} key={generateString()} activePart={activePart}/>
    ));
  };

  const tabs = ownDictionary.map((part: any) => {
    return (
      <div className="flex-btn-group" key={generateString()}>
        <Button 
          marginY={8} 
          marginRight={12}
          key={generateString()} 
          onClick={() => {
            setActivePart(part.part);
          }}
          onDoubleClick={() => setNewActivePartOptions(part.part)}
        >
          {part.part}
        </Button>
        <div className={`${activePartOptions === part.part? "open-part" : "close-part"} part-option`}>
          <Button 
            marginY={8} 
            marginRight={12}
            key={generateString()} 
          >
            make test
          </Button>
          <Button 
            marginY={8} 
            marginRight={12}
            key={generateString()} 
          >
            shuffle
          </Button>
        </div>
      </div>
    )
  });

  return (
    <div>
      <Row
        Left={<div>
          <div className="dictionary-tabs">
            { tabs }
          </div>
          <Table className="bg-light" style={{height: "100%"}}>{listWords(ownDictionary)}</Table>
        </div>}
        Right={<SearchWord />}
      />
    </div>
  )
};

export default OwnDictionary;
